
import { defineComponent } from 'vue';
import {
  translate,
  Matomo,
  AjaxHelper,
  ContentBlock,
  ContentTable,
  MatomoUrl, NotificationsStore,
} from 'CoreHome';
import { Field } from 'CorePluginsAdmin';

interface Option {
  key: string;
  value: string;
}

interface LogEntry {
  severity: string;
  tag: string;
  datetime: string;
  requestId: string;
  message: string;
}

interface LogViewerState {
  page: number;
  query: string;
  isLoading: boolean;
  logWriters: Option[];
  selectedLogWriter: string;
  useRegExp: boolean;
  severities: Option[];
  selectedSeverity: string;
  logs?: LogEntry[];
  logConfig?: unknown;
}

declare global {
  interface Window {
    preg_quote(str: string, delimiter?: string): string;
  }
}

export default defineComponent({
  props: {
    limit: {
      type: Number,
      required: true,
    },
  },
  components: {
    ContentBlock,
    Field,
  },
  directives: {
    ContentTable,
  },
  data(): LogViewerState {
    return {
      page: 0,
      query: '',
      isLoading: true,
      logWriters: [],
      selectedLogWriter: '',
      useRegExp: false,
      severities: [
        {
          key: '',
          value: translate('LogViewer_AnySeverity'),
        },
        {
          key: 'DEBUG',
          value: 'DEBUG',
        },
        {
          key: 'INFO',
          value: 'INFO',
        },
        {
          key: 'NOTICE',
          value: 'NOTICE',
        },
        {
          key: 'WARNING',
          value: 'WARNING',
        },
        {
          key: 'ERROR',
          value: 'ERROR',
        },
        {
          key: 'CRITICAL',
          value: 'CRITICAL',
        },
        {
          key: 'ALERT',
          value: 'ALERT',
        },
        {
          key: 'EMERGENCY',
          value: 'EMERGENCY',
        },
      ],
      selectedSeverity: '',
      logs: undefined,
      logConfig: undefined,
    };
  },
  setup() {
    let fetchLogEntriesAbort: AbortController|null = null;

    const getLogEntries = (params: QueryParameters) => {
      if (fetchLogEntriesAbort) {
        fetchLogEntriesAbort.abort();
        fetchLogEntriesAbort = null;
      }

      fetchLogEntriesAbort = new AbortController();

      return AjaxHelper.fetch(
        {
          ...params,
          method: 'LogViewer.getLogEntries',
        },
        {
          abortController: fetchLogEntriesAbort,
        },
      ).finally(() => {
        fetchLogEntriesAbort = null;
      });
    };

    return {
      getLogEntries,
    };
  },
  created() {
    AjaxHelper.fetch({
      method: 'LogViewer.getAvailableLogReaders',
    }).then((logWriters) => {
      if (Array.isArray(logWriters)) {
        this.logWriters = logWriters.map((logWriter) => ({
          key: logWriter,
          value: logWriter,
        }));
      }
    }).then(
      () => AjaxHelper.fetch({ method: 'LogViewer.getConfiguredLogReaders' }),
    ).then((logWriters) => {
      if (logWriters?.[0]) {
        [this.selectedLogWriter] = logWriters;
      } else {
        const title = translate('LogViewer_NoSupportedLogWriterConfiguredTitle');
        const message = translate('LogViewer_NoSupportedLogWriterConfiguredMessage', [
          '<a href="https://piwik.org/faq/troubleshooting/faq_115/">',
          '</a>',
          this.logWriters.map((item) => item.value).join(', '),
        ]);

        NotificationsStore.show({
          message,
          title,
          context: 'info',
          noclear: true,
          type: 'transient',
        });

        if (this.logWriters?.[0]) {
          this.selectedLogWriter = this.logWriters[0].value;
        }
      }

      if (this.selectedLogWriter) {
        this.update();
      } else {
        this.isLoading = false;
      }
    });

    AjaxHelper.fetch({
      method: 'LogViewer.getLogConfig',
      filter_limit: '-1',
    }).then((config) => {
      this.logConfig = config;
    });
  },
  methods: {
    update() {
      this.isLoading = true;

      this.logs = [];
      this.getLogEntries({
        query: this.buildQuery,
        limitPerPage: this.limit,
        source: this.selectedLogWriter,
        page: this.page,
      }).then((logs) => {
        this.logs = logs;
      }).finally(() => {
        this.isLoading = false;
      });
    },
    searchSeverity(severity: string) {
      if (!severity) {
        return;
      }

      this.selectedSeverity = severity.toUpperCase();
      this.update();
    },
    searchDate(datetime: string) {
      if (!datetime) {
        return;
      }

      this.selectedSeverity = '';
      this.query = `\\[${datetime.substr(0, 10)}.{9}\\]`;
      this.useRegExp = true;
      this.update();
    },
    searchRequestId(requestId: string) {
      if (!requestId) {
        return;
      }

      this.searchText(`[${requestId}]`);
    },
    searchText(text: string) {
      if (!text) {
        return;
      }

      this.selectedSeverity = '';
      this.query = text;

      if (this.useRegExp) {
        this.query = window.preg_quote(this.query);
      }

      this.update();
    },
    previousPage() {
      if (this.page > 0) {
        this.page -= 1;
        this.update();
      }
    },
    nextPage() {
      if (this.logs?.length) {
        this.page += 1;
        this.update();
      }
    },
    onSearchKeydown(event: KeyboardEvent) {
      if (event.code !== 'Enter') {
        return;
      }

      this.update();
    },
  },
  computed: {
    buildQuery() {
      let { query } = this;

      if (!this.useRegExp) {
        query = window.preg_quote(this.query);
      }

      if (this.selectedSeverity) {
        query = `^${this.selectedSeverity}(.*)${query}`;
      }

      return query;
    },
    exportFormAction() {
      return `?${MatomoUrl.stringify({
        module: 'API',
        method: 'LogViewer.getLogEntries',
        query: this.buildQuery,
        format: 'TSV',
        page: 0,
        limitPerPage: 5000,
      })}`;
    },
    logConfigJson() {
      return JSON.stringify(this.logConfig || {});
    },
    tokenAuth() {
      return Matomo.token_auth;
    },
    logsWithMessage() {
      return (this.logs || []).filter((l) => l.message);
    },
  },
});
